import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
  // 定义状态
  state: {
    author: 'A.Q.I',
    listData : '',
    listMount : true,
    posState : ''
  },
  mutations: {
    SAVE_POSITION (state, payload) {
      state.posState = payload
    }
  }
});

export default store



